* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: "Nunito", sans-serif;
  align-items: center;
}

button {
  border: none;
  outline: none;
  width: 100%;
  padding: 15px 0;
  color: #fff;
  font-size: 16px;
  letter-spacing: 1px;
  background: #603bbb;
  cursor: pointer;
}

nav {
  background: linear-gradient(315deg, #182b3a 0%, #20a4f3 74%);
  width: 100%;
  padding: 20px 40px;
  display: flex;
  align-items: center;
  justify-content: center;
}

nav h2 {
  color: #fff;
}

nav button {
  width: 86px;
  padding: 8px 0;
  background: #3a2b58;
}
